.pattern {
  background-color: #202222;
  position: relative;
  display: block;
}

.pattern:before {
  content: '';
  opacity: 0;
  transition: opacity .25s ease, top .25s ease, left .25s ease, bottom .25s ease, right .25s ease;
  z-index: 1;
  position: absolute;
  border: 1px solid #fff;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.pattern:hover:before {
  top: 10px;
  right: 10px;
  left: 10px;
  bottom: 10px;
  opacity: 0.2;
}

.pattern .overlay {
  background-position: center center;
  background-size: cover;
  position: relative;
  display: block;
}

.pattern .overlay:before {
  content: '';
  opacity: 0.55;
  background-color: #202222;
  transition: opacity .25s ease;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.pattern:hover .overlay::before,
.pattern .overlay:hover::before {
  opacity: 0.75;
}

